

























import { Component, Prop, Watch, Vue } from 'vue-property-decorator';
import { GameData } from '@/globalInterface';

@Component
export default class CommonGameSelector extends Vue {
  @Prop({type: Boolean, default: false}) public readonly showactive!: boolean;
  @Prop({type: Boolean, default: false}) public readonly isSingle!: boolean;
  @Prop({type: String, default: 'gameSelector'}) public readonly selectorType!: string;
  private selectedIndex: {[T: string]: number[]} = {};
  private activeNames: string[] = [];
  private games: GameData[] = [
    {
      id: 5,
      text: '热门彩',
      children: [
        {
          id: 8,
          text: '重庆欢乐生肖',
        },
        {
          id: 2,
          text: '幸运时时彩',
        },
        {
          id: 3,
          text: 'SG飞艇',
        },
        {
          id: 5,
          text: '幸运飞艇',
        }
      ]
    }
  ];
  private localActive(gameName: string) {
    if (this.activeNames.includes(gameName) === true) {
      return true;
    }
  }
  private toggleSelect(gameType: number, gameId: number, gameName: string) {
    if (this.selectorType === 'gameSelector') {
      if (this.isSingle) {
        this.selectedIndex = {};
        this.activeNames = [];
      }
      if (this.isActive(gameType, gameId)) {
        this.selectedIndex[gameType] = this.selectedIndex[gameType].filter(item => item !== gameId);
        this.activeNames = this.activeNames.filter(item => item !== gameName);
      } else {
        if(!this.selectedIndex[gameType]) {
          this.selectedIndex[gameType] = [];
        }
        this.selectedIndex[gameType].push(gameId);
        this.activeNames.push(gameName);
      }
    } else {
      this.$emit('index-change', {
        gameType: gameType,
        gameId: gameId,
        gameName: gameName
      });
    }
  }
  private isActive(gameType: number, gameId: number) {
    if (this.selectedIndex[gameType]) {
      if (this.selectedIndex[gameType].length > 0) {
        if (this.selectedIndex[gameType].includes(gameId) === true) {
          return true;
        }
      }
    }
    return false;
  }
  private cancel() {
    this.$emit('force-cancel');
  }
  private confirm() {
    this.$emit('index-change', {
      selectedIndex: this.selectedIndex,
      activeNames: this.activeNames
    });
  }
}
