
























import { Component, Vue } from 'vue-property-decorator';
import CommonGameSelector from './CommonGameSelector.vue';

@Component({
  components: {
    CommonGameSelector
  }
})
export default class Navigation extends Vue{
  private gamePickerShow?: boolean = false;
  private goPage(url: string) {
    const currName = this.$route.name;
    if (currName != url) {
      this.$router.push(url);
    }
  }
  private gamesChange(value: any) {
    this.gamePickerShow = false;
    console.log(value);
  }
}
